<template>
    <el-table
            :data="tableData"
            style="width: 100%">
        <el-table-column type="expand">
            <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="">
                        <span>{{ props.row.type }}</span>
                    </el-form-item>
                    <el-form-item label=" ID">
                        <span>{{ props.row.id }}</span>
                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ props.row.desc }}</span>
                    </el-form-item>
                </el-form>
            </template>
        </el-table-column>
        <el-table-column
                label=" ID"
                prop="id">
        </el-table-column>
        <el-table-column
                label=""
                prop="type">
        </el-table-column>
        <el-table-column
                label=""
                prop="desc">
        </el-table-column>
    </el-table>
</template>

<script>
    import api from '../../api'
    export default {
        name: "AllKnowledges",
        data() {
            return {
                knowledgeList: [],
                tableData: [
                    {
                        id: '12987122',
                        type: '', // //
                        desc: '1+1=2', //
                    },]
            }
        },
        mounted() {
            this.getAllKnowledges()
        },
        methods: {
            getAllKnowledges() {
                api.getAllKnowledges().then(response => {
                    let data = response.data.content;
                    let total = data.length;
                    for (let i = 0;i < total; i ++) {
                        this.tableData.push({
                            id: i,
                            type: '',
                            desc: data[i]['content']
                        })
                    }
                }).catch(err => {
                    console.log(err.response);
                });
            }
        }
    }

</script>

