<template>
    <div>
<!--        <span @click="drawEcharts"></span>-->
        <div class="gameChart center" id="gameChart">

        </div>

    </div>

</template>

<script>
    //   
    import api from '../../api'
    // import formatDate from '../../utils'
    export default {
        name: "GameNum",
        data() {
            return {
                time_range: [],
                time_range_num: [],
            }
        },
        mounted() {
            this.drawEcharts()
        },

        methods: {
            drawEcharts() {
                var myChart = this.$echarts.init(document.getElementById("gameChart"), 'azul');
                api.getGameNums().then(response => {
                    if (response.status === 200) {
                        if (response.data.code === 200) {
                            console.log('success')
                            // let time_range_ = response.data.content["timeRange"];
                            // console.log(time_range_)
                            // for (var i = 0;i < time_range_.length; i++) {
                            //     this.time_range.push(formatDate(time_range_[i]))
                            // }
                            this.time_range = response.data.content["timeRange"];
                            this.time_range_num = response.data.content["timeRangeNum"];
                            console.log(this.time_range)
                            console.log(this.time_range_num)
                            myChart.setOption({
                                title: {
                                    text: ''
                                },
                                // 
                                tooltip : {
                                    trigger: 'axis',
                                    axisPointer: {
                                        type: 'cross',
                                        label: {
                                            backgroundColor: '#6a7985'
                                        }
                                    }
                                },

                                //  
                                legend: {
                                    align : 'right',
                                    x : 'right',
                                    top : 25,
                                    selectedMode : 'single',    //  
                                    data:['']            //  
                                },

                                //  xy
                                xAxis: {
                                    type: 'category',
                                    boundaryGap: false,
                                    data: this.time_range
                                },
                                yAxis: {
                                    type: 'value'
                                },
                                // 
                                series: [
                                    {
                                        name : '',
                                        data: this.time_range_num,
                                        type: 'line',
                                        smooth: true,   //  
                                        areaStyle: {},
                                    }

                                ]
                            }, true)
                        }
                    }


                }).catch(err => {
                    console.log(err.response);
                })

            }
        }
    }
</script>

<style scoped>
    .gameChart {
        width: 500px;
        height: 300px;
    }

    .center {
        margin: 20px auto;
    }

